<template lang="">
    <div class="height100">
        <ul class="tab-nav normal-box">
            <li v-for="(item,index) in tabNavs" @click="selectTab(index)"  :class="{active:tabNavs[index].active}">
                <router-link :to="item.path">{{item.name}}</router-link>
            </li>
        </ul>
        <router-view ></router-view>
    </div>

</template>
<script>
import cookieUtil from 'js-cookie'
import {MessageBox} from 'mint-ui'
export default{
    data(){
        return {
            conferNo:"",
            selected:"",
            bb:true,
            trdAcct:"",//证券账户
            hqzqdm:"",//证券代码
            maxnum:"",//
            orderQty:"",//
            orderPrice:'',
            bargainOrderPrice:'',//成交价格
            bargainOrderQty:'',//成交数量
            tabNavs:[{selectedId:global.STKBIZTYPE.BUY_IN,name:"买入",active:false,path:"/trade/buyIn"},
                    {selectedId:global.STKBIZTYPE.SELL_OUT,name:"卖出",active:false,path:"/trade/sellOut"},
                    {selectedId:global.STKBIZTYPE.REVOKE,name:"撤单",active:false , path:"/trade/cancelOrder"},
                    {selectedId:global.STKBIZTYPE.POSITION,name:"持仓",active:false,path:"/trade/myHold" },
                    {selectedId:"cust101",name:"查询",active:false,path:"/trade/search"},
                ]
            }
    },
    created:function(){
        // axiosUtil.isLogin().then(message=>{
        //     var message=message.data;
        //   if(message.errorcode==0){//是否登录
                this.trdAcct=cookieUtil.get("trdAcct")
                if(this.$route.query.hqzqdm){
                    this.hqzqdm=this.$route.query.hqzqdm;
                }
                if(this.$route.query.conferNo){
                    this.conferNo=this.$route.query.conferNo
                }
                if(this.$route.query.bargainOrderPrice){
                    this.bargainOrderPrice=this.$route.query.bargainOrderPrice
                }
                if(this.$route.query.type){
                    this.selected=this.$route.query.type;
                    for(let i=0;i<this.tabNavs.length;i++){
                        let item=this.tabNavs[i];
                        if(this.selected===item.selectedId){
                            item.active=true;
                            var par ="";
                            if( this.hqzqdm!=""){
                                par="?hqzqdm="+this.hqzqdm
                            }
                            this.$router.push(this.tabNavs[0].path+par)
                            this.$router.push(this.tabNavs[i].path)
                            break;
                        }
                    }
                }else{


                    this.selected=global.STKBIZTYPE.BUY_IN;///默认买入
                    this.tabNavs[0].active=true;
                    for(let ia=0;ia<this.tabNavs.length;ia++){
                        let item=this.tabNavs[ia];
                        if(this.$route.path==item.path){
                            this.selected=item.selectedId;
                            this.tabNavs[0].active=false;
                             this.tabNavs[ia].active=true;
                            break;
                        }
                    }

                    var par ="";
                    if( this.hqzqdm!=""){
                        par="?hqzqdm="+this.hqzqdm
                    }
                    if(this.$route.name=="trade"){
                   this.$router.push(this.tabNavs[0].path+par)
                   }else{
                    this.$router.push(this.$route.path);
                   }
                   // if(this.$route.name=="sellOut"){
                   //   this.selected="1";
                   // }
                   // if(this.$route.name=="buyIn"){
                   //   this.selected="0";
                   // }

                }
                    //that.$router.push("/novelList/detail?novelId="+novelId);
            // }else{
            //         this.$router.push("/login");
            // }

        //})

    },
    methods:{
        selectTab:function(index){
            // if(index!=5 ){
            //     for(let i=0;i<this.tabNavs.length;i++){
            //         let item=this.tabNavs[i];
            //         item.active=false;
            //         if(item.selectedId===this.tabNavs[index].selectedId){
            //             this.tabNavs[i].active=true;
            //             var par ="";
            //                 if( this.hqzqdm!=""){
            //                     par="?hqzqdm="+this.hqzqdm
            //                 }
            //             this.$router.push(this.tabNavs[i].path+par)
            //         }
            //     }
            //     this.selected=this.tabNavs[index].selectedId;

            // }


        }
    }
}

</script>
<style scoped>
.tab-nav li{

}
    .tab-nav .active{
        /*border-bottom: 2px solid #F06969;
        color :#F06969;*/
    }
    .tab-nav a{
        width: 100%;
        display: block;
        font-size: 14px;
        color:#666;
        padding: 6px;
        text-decoration: none;
    }
.tab-nav .router-link-active{
    border-bottom: 2px solid #f06969;
    color: #f06969;
}

</style>



